* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  background-color: azure;
  height: 100%;
}

html,
body,
#notRoot,
.container {
  height: 100%;
  display: flex;
  justify-content: center;
}

.loader {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.movies {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  flex-wrap: wrap;
  padding: 50px;
  padding-top: 70px;
  width: 85%;
}
