.movies .movie {
  width: 45%;
  background-color: white;
  margin-bottom: 70px;
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  font-weight: 300;
  border-radius: 5px;
  padding: 20px;
  color: darkgrey;
  box-shadow: 0 13px 27px -5px;
}
.movie .movie_data {
  max-width: auto;
  width: 100%;
}

.movie img {
  position: relative;
  max-width: 180px;
  width: 100%;
  top: -50px;
  max-width: 120px;
  margin-right: 30px;
  box-shadow: 0 30px 60px -12px rgba(50, 50, 93, 0.25),
    0 18px 36px -18px rgba(0, 0, 0, 0.3), 0 -12px 36px -8px rgba(0, 0, 0, 0.025);
}
.movie .movie_title .movie_year {
  margin: 0;
  font-weight: 300;
}

.movie .movie_title {
  margin-bottom: 5px;
  font-size: 24px;
  color: darkslategray;
}
.movie .movie_year {
  margin: 0;
  margin-top: 3;
}

.movie .movie_genres li,
.movie .movie_year {
  margin-right: 10px;
  font-size: 14px;
}

.movie .movie_genres {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  margin: 5px 0px;
}
